.slick-dots li button {
    background: 0 0;
    border: 0;
    color: transparent;
    cursor: pointer;
    display: block;
    font-size: 0;
    height: 20px;
    line-height: 0;
    outline: 0;
    padding: 5px;
    width: 20px;
}



